input[type="radio"] {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    position: relative;
    margin-right: 5px;
    top: 4px;
    width: var(--s1);
    height: var(--s1);
    border-radius: 50%;
    background: var(--blue);
    border: 5px solid var(--blue);
    transition: 0.1s;
}

input:checked {
    background: var(--yellow);
}
